import React, { useEffect, useState, useRef } from 'react'
import axios from 'axios'
import InfiniteScroll from 'react-infinite-scroller'
import message from 'antd/es/message'

import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard from '../../components/MasonryCard'
import Helmet from 'react-helmet'

import 'leaflet/dist/leaflet.css'
import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    Box,
    createStyles,
    Divider,
    IconButton,
    InputBase,
    makeStyles,
    Paper,
    Theme,
} from '@material-ui/core'
import { LocationOn, SearchOutlined } from '@material-ui/icons'
import MapSettings from './MapSettings'
import Layout from '../../components/Layout/Layout'
import { Link } from 'react-router-dom'
import ShareButton from '../../components/UI/ShareButton'
const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'relative',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const Explore = () => {
    const map = useRef(null)
    const [isSearching, setIsSearching] = useState(false)
    const [mapVisible, setMapVisible] = useState(false)
    const [foundProgresses, setFoundProgresses] = useState([])
    const [progresses, setProgresses] = useState([])
    const [noMore, setNoMore] = useState(false)
    const [noMorePopular, setNoMorePopular] = useState(false)

    const [search, setSearch] = useState({
        value: '',
        withMap: false,
        distance: 20,
        zoom: 7,
        position: [40.689078, -74.044526],
        units: 'mi',
    })

    const [value, setValue] = useState('')

    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch().addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [map.current])

    const onSearch = (value, noNoMore) => {
        if (!noMore || noNoMore) {
            setNoMore(true)
            axios
                .post('/api/explore/search', {
                    value: value ? { ...search, value } : search,
                    skip: noNoMore ? 0 : foundProgresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMore(res.data.noMore)
                        setFoundProgresses(res.data.progresses)
                        if (res.data.progresses.length === 0)
                            message.warning('There are no progresses found.')
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }
    const onGetPopular = () => {
        if (!noMorePopular) {
            setNoMorePopular(true)
            axios
                .post('/api/explore/popular', {
                    skip: progresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMorePopular(res.data.noMore)
                        setProgresses([...progresses, ...res.data.progresses])
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }

    const classes = useStyles()
    return (
        <Layout
            header={
                <Paper className={classes.root} variant="outlined">
                    <InputBase
                        className={classes.input}
                        placeholder="Search goals"
                        onChange={e => {
                            if (!e.target.value && !search.withMap) {
                                setNoMore(false)
                                setFoundProgresses([])
                                setIsSearching(false)
                                setSearch({ ...search, value: '' })
                            }
                            setValue(e.target.value)
                        }}
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => {
                            if (value) {
                                setIsSearching(true)
                                if (value !== search.value) {
                                    setNoMore(false)
                                    setFoundProgresses([])
                                    setSearch({ ...search, value })
                                    onSearch(value, true)
                                }
                            } else {
                                setNoMore(false)
                                setSearch({ ...search, value })
                                setIsSearching(false)
                            }
                        }}
                    >
                        <SearchOutlined />
                    </IconButton>
                    <Divider
                        className={classes.divider}
                        orientation="vertical"
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => setMapVisible(true)}
                        color={search.withMap ? 'primary' : 'default'}
                    >
                        <LocationOn />
                    </IconButton>
                </Paper>
            }
        >
            <div>
                <Helmet>
                    <title>
                        Explore goals and ideas from around the World!
                    </title>
                    <meta
                        name="description"
                        content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                    />
                </Helmet>

                <MapSettings
                    mapVisible={mapVisible}
                    setMapVisible={setMapVisible}
                    setIsSearching={setIsSearching}
                    setNoMore={setNoMore}
                    setFoundProgresses={setFoundProgresses}
                    onSearch={onSearch}
                    onGetPopular={onGetPopular}
                    setSearch={setSearch}
                    search={search}
                />
                <Box
                    style={{
                        textAlign: 'center',
                        display: 'flex',
                        justifyContent: 'center',
                        padding: '10px',
                        marginBottom: '20px',
                    }}
                >
                    <Link
                        to="/"
                        style={{
                            display: 'flex',
                            alignItems: 'baseline',
                        }}
                    >
                        <img
                            style={{
                                height: '23px',
                                marginRight: '4px',
                            }}
                            src="/logo.jpg"
                        />
                        Addspire
                    </Link>
                </Box>
                {isSearching ? (
                    foundProgresses.length > 0 ? (
                        <InfiniteScroll
                            pageStart={0}
                            loadMore={onSearch()}
                            hasMore={!noMore}
                            loader={
                                <div className="loader" key={0}>
                                    Loading ...
                                </div>
                            }
                        >
                            <MasonryGrid>
                                {foundProgresses
                                    .sort((a, b) =>
                                        a.views > b.views ? -1 : 1
                                    )
                                    .map((item, index) => (
                                        <MasonryCard
                                            id={item._id}
                                            key={index}
                                            onClickLink={'/goals/' + item._id}
                                            images={item.goal.images}
                                            body={
                                                <>
                                                    {/* <UserButton
                                                noName={true}
                                                id={item.owner}
                                                friendDataExp={friendData}
                                            /> */}
                                                    <span
                                                        style={{
                                                            fontWeight: 'bold',
                                                        }}
                                                    >
                                                        {item.name}
                                                    </span>
                                                </>
                                            }
                                            actions={
                                                <ShareButton
                                                    link={
                                                        'https://addspire.com/goals/' +
                                                        item._id
                                                    }
                                                    title={
                                                        item.name +
                                                        ' goal on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                                    }
                                                    media={
                                                        item.goal.images &&
                                                        item.goal.images[0]
                                                    }
                                                />
                                            }
                                        />
                                    ))}
                            </MasonryGrid>
                        </InfiniteScroll>
                    ) : null
                ) : (
                    <InfiniteScroll
                        pageStart={0}
                        loadMore={onGetPopular}
                        hasMore={!noMorePopular}
                        loader={
                            <div className="loader" key={0}>
                                Loading ...
                            </div>
                        }
                    >
                        <MasonryGrid>
                            {progresses
                                .sort((a, b) => (a.views > b.views ? -1 : 1))
                                .map((item, index) => (
                                    <MasonryCard
                                        id={item._id}
                                        key={index}
                                        onClickLink={'/goals/' + item._id}
                                        images={item.goal.images}
                                        body={
                                            <>
                                                {/* <UserButton
                                            noName={true}
                                            id={item.owner}
                                            friendDataExp={friendData}
                                        /> */}
                                                <span
                                                    style={{
                                                        fontWeight: 'bold',
                                                    }}
                                                >
                                                    {item.name}
                                                </span>
                                            </>
                                        }
                                        actions={
                                            <ShareButton
                                                link={
                                                    'https://addspire.com/goals/' +
                                                    item._id
                                                }
                                                title={
                                                    item.name +
                                                    ' goal on Addspire.com - a social network for sharing and supporting ideas, promises and resolutions.'
                                                }
                                                media={
                                                    item.goal.images &&
                                                    item.goal.images[0]
                                                }
                                            />
                                        }
                                    />
                                ))}
                        </MasonryGrid>
                    </InfiniteScroll>
                )}
            </div>
        </Layout>
    )
}

export default Explore
