import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import Form from 'antd/es/form'

import { descriptionRules, nameRules } from '../../constants/fieldRules'
import ImageCarousel from '../UI/ImageCarousel/ImageCarousel'
import {
    Card,
    CardActionArea,
    CardContent,
    CardMedia,
    Grid,
    IconButton,
    makeStyles,
    Typography,
    Button,
    Menu,
    MenuItem,
    Dialog,
    Slide,
    AppBar,
    Toolbar,
    DialogTitle,
    Input,
    DialogActions,
    Box,
} from '@material-ui/core'
import { AddOutlined, Close, MoreVertOutlined } from '@material-ui/icons'
import { red } from '@material-ui/core/colors'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import TinyEditor from '../UI/TinyEditor'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import { getPH, placeholderImages } from '../MasonryCard'
import { spanStyle } from '../Post/Message'
import moment from 'moment'
moment.updateLocale('en', {
    relativeTime: {
        future: 'in %s',
        past: '%s',
        s: 'now',
        ss: 'now',
        m: '1 min',
        mm: '%d mins',
        h: '1 hr',
        hh: '%d hrs',
        d: '1 day',
        dd: '%d days',
        w: '1 wk',
        ww: '%d wks',
        M: '1 mth',
        MM: '%d mths',
        y: 'a year',
        yy: '%d years',
    },
})
moment().format()
const useStyles = makeStyles(theme => ({
    root: {
        // maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    appBar: {
        position: 'fixed',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
}))
const TransitionComponent = props => <Slide direction="left" {...props} />

const Rewards = props => {
    const rewards = props.rewards || []

    const classes = useStyles()

    const [previewReward, setPreviewReward] = useState()
    const [previewRewardIndex, setPreviewRewardIndex] = useState()
    const [editedReward, setEditedReward] = useState()
    const [editedRewardIndex, setEditedRewardIndex] = useState()
    const { account, readOnly, progress } = props

    const [anchorEl, setAnchorEl] = useState(null)
    const [confirmOpen, setConfirmOpen] = useState(false)
    const handleClick = event => {
        setAnchorEl(event.currentTarget)
    }
    const handleClose = () => {
        setAnchorEl(null)
        setConfirmOpen(false)
    }

    const layout = {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 },
    }

    const saveReward = editedReward => {
        let rewardToSave = {}

        rewardToSave = { ...editedReward }
        delete rewardToSave.transaction

        rewardToSave.owner = account._id
        if (props.onValueChange) {
            let newRewards = [...rewards]

            if (editedRewardIndex === 'new')
                newRewards = [...newRewards, rewardToSave]
            else
                newRewards = newRewards.map((item, index) => {
                    if (index === editedRewardIndex) return rewardToSave
                    else return item
                })
            props.onValueChange(newRewards)
            setEditedReward()
            setEditedRewardIndex()
        } else {
            wsActions.saveReward(account._id, progress._id, rewardToSave)
            setEditedReward()
            setEditedRewardIndex()
        }
        setPreviewReward(rewardToSave)
    }

    return (
        <>
            <HorizontalScrollList
                title={props.title}
                items={rewards}
                empty={props.empty}
                withAdd={!readOnly}
                addTitle="Add inspiration"
                onAdd={() => {
                    setEditedReward({})
                    setEditedRewardIndex('new')
                }}
                content={(item, index) => (
                    <>
                        <CardActionArea
                            onClick={() => {
                                setPreviewReward(item)
                                setPreviewRewardIndex(index)
                            }}
                        >
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.itemImages.length > 0
                                        ? item.itemImages[0]
                                        : placeholderImages[
                                              getPH(item.rewardId)
                                          ]
                                }
                                title={item.itemName}
                            />
                            <CardContent>
                                <Typography variant="body2" noWrap>
                                    {item.itemName}
                                </Typography>
                                {props.withDate && (
                                    <span style={spanStyle}>
                                        {moment(
                                            item.transaction.date
                                        ).fromNow()}
                                    </span>
                                )}
                            </CardContent>
                        </CardActionArea>
                        {props.isOwner && props.withConfirm && (
                            <Button
                                color="primary"
                                variant="contained"
                                onClick={() =>
                                    wsActions.confirmTransaction(
                                        account._id,
                                        item.transaction._id
                                    )
                                }
                            >
                                Received
                            </Button>
                        )}
                    </>
                )}
            />

            <Dialog
                fullScreen
                open={Boolean(editedRewardIndex) || Boolean(editedReward)}
                onClose={() => {
                    setEditedReward()
                    setEditedRewardIndex()
                }}
                TransitionComponent={TransitionComponent}
            >
                {(Boolean(editedRewardIndex) || Boolean(editedReward)) && (
                    <>
                        <AppBar className={classes.appBar} color="primary">
                            <Toolbar>
                                <IconButton
                                    edge="start"
                                    color="inherit"
                                    onClick={() => {
                                        setEditedReward()
                                        setEditedRewardIndex()
                                    }}
                                    aria-label="close"
                                >
                                    <Close />
                                </IconButton>
                                <Typography
                                    variant="h6"
                                    className={classes.title}
                                    color="inherit"
                                >
                                    {editedRewardIndex === 'new'
                                        ? 'Add new inspiration'
                                        : 'Edit inspiration'}
                                </Typography>
                                <Button
                                    autoFocus
                                    color="inherit"
                                    onClick={() => {
                                        saveReward(editedReward)
                                    }}
                                >
                                    Save
                                </Button>
                            </Toolbar>
                        </AppBar>
                        <Toolbar />
                        <Form
                            style={{
                                maxWidth: '1200px',
                                margin: '0 auto',
                                padding: '15px',
                                width: '100%',
                            }}
                            name="new_inspiration"
                            {...layout}
                        >
                            <Form.Item
                                name="images"
                                label="Images"
                                // valuePropName="images"
                            >
                                <ImagePickerMulti
                                    images={editedReward.itemImages}
                                    onValueChange={itemImages =>
                                        setEditedReward({
                                            ...editedReward,
                                            itemImages,
                                        })
                                    }
                                />
                            </Form.Item>

                            <Form.Item label="Name" rules={nameRules}>
                                <Input
                                    fullWidth={true}
                                    value={editedReward.itemName}
                                    onChange={({ target: { value } }) =>
                                        setEditedReward({
                                            ...editedReward,
                                            itemName: value,
                                        })
                                    }
                                />
                            </Form.Item>
                            <Form.Item
                                label="Description"
                                rules={descriptionRules}
                            >
                                <TinyEditor
                                    value={editedReward.itemDescription}
                                    onEditorChange={value =>
                                        setEditedReward({
                                            ...editedReward,
                                            itemDescription: value,
                                        })
                                    }
                                />
                            </Form.Item>
                        </Form>
                    </>
                )}
            </Dialog>
            <Dialog
                fullScreen
                open={Boolean(previewReward)}
                onClose={() => setPreviewReward()}
                TransitionComponent={TransitionComponent}
            >
                {previewReward && (
                    <>
                        <AppBar className={classes.appBar} color="primary">
                            <Toolbar>
                                <IconButton
                                    edge="start"
                                    color="inherit"
                                    onClick={() => setPreviewReward()}
                                >
                                    <Close />
                                </IconButton>
                                <Typography
                                    variant="h6"
                                    className={classes.title}
                                    color="inherit"
                                >
                                    Inspiration details
                                </Typography>
                            </Toolbar>
                        </AppBar>
                        <Toolbar />
                        <Box
                            style={{
                                maxWidth: '1200px',
                                margin: '0 auto',
                                padding: '30px 15px 15px',
                                width: '100%',
                            }}
                        >
                            <ImageCarousel images={previewReward.itemImages} />
                            {!props.onlyButton &&
                                !props.readOnly &&
                                (account._id === previewReward.owner ||
                                    account._id === progress.owner) && (
                                    <>
                                        <Box style={{ textAlign: 'center' }}>
                                            <IconButton onClick={handleClick}>
                                                <MoreVertOutlined />
                                            </IconButton>
                                        </Box>
                                        <Menu
                                            id="simple-menu"
                                            anchorEl={anchorEl}
                                            keepMounted
                                            open={Boolean(anchorEl)}
                                            onClose={handleClose}
                                        >
                                            {account._id ===
                                                previewReward.owner && (
                                                <MenuItem
                                                    onClick={() => {
                                                        setEditedReward(
                                                            previewReward
                                                        )
                                                        setEditedRewardIndex(
                                                            previewRewardIndex
                                                        )
                                                        handleClose()
                                                    }}
                                                >
                                                    Edit
                                                </MenuItem>
                                            )}
                                            <MenuItem
                                                onClick={() => {
                                                    setConfirmOpen(true)
                                                }}
                                            >
                                                Delete
                                            </MenuItem>
                                        </Menu>
                                        <Dialog
                                            open={Boolean(confirmOpen)}
                                            onClose={handleClose}
                                        >
                                            <DialogTitle>
                                                Are youe sure you want to delete
                                                this reward?
                                            </DialogTitle>

                                            <DialogActions>
                                                <Button
                                                    onClick={handleClose}
                                                    color="primary"
                                                >
                                                    Disagree
                                                </Button>
                                                <Button
                                                    onClick={() => {
                                                        if (
                                                            props.onValueChange
                                                        ) {
                                                            props.onValueChange(
                                                                props.rewards.filter(
                                                                    (
                                                                        item,
                                                                        indexIn
                                                                    ) =>
                                                                        indexIn !==
                                                                        props.index
                                                                )
                                                            )
                                                        } else {
                                                            wsActions.deleteReward(
                                                                account._id,
                                                                progress._id,
                                                                props.item
                                                                    .rewardId
                                                            )
                                                        }
                                                        handleClose()
                                                    }}
                                                    color="primary"
                                                >
                                                    Agree
                                                </Button>
                                            </DialogActions>
                                        </Dialog>
                                    </>
                                )}
                            <Typography
                                variant="h4"
                                style={{
                                    textAlign: 'center',
                                    margin: '20px 0',
                                }}
                            >
                                {previewReward.itemName}
                            </Typography>
                            <div
                                dangerouslySetInnerHTML={{
                                    __html: previewReward.itemDescription,
                                }}
                            />
                        </Box>
                    </>
                )}
            </Dialog>
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        friendData: state.friendData,
        progress: state.progress,
    }
}

export default withRouter(connect(mapStateToProps)(Rewards))
